import { Location } from '@reach/router';
import React from 'react';
import classnames from 'classnames';
import { Link } from 'gatsby';
import { Icon } from 'carbon-components-react';

const getPathArray = location => location.pathname.split('/');

export default class SideNavItem extends React.Component {
  state = {
    open: false,
    active: false,
  };

  toggleSubNav = () => {
    this.setState({
      open: !this.state.open,
    });
  };

  renderSubNavItems = (subItems, location) =>
    Object.keys(subItems).map(item => {
      const subNavClasses = classnames('side-nav__sub-nav-item', {
        'side-nav__sub-nav-item--active': getPathArray(location).indexOf(item) !== -1,
      });
      return (
        <li className={subNavClasses} key={item}>
          <Link activeClassName="side-nav__sub-nav-item--active" to={`/${this.props.itemSlug}/${item}`}>
            {subItems[item].title}
          </Link>
        </li>
      );
    });

  render() {
    const { item, itemSlug } = this.props;
    const hasSubNav = !(item['sub-nav'] === undefined);

    return (
      <Location>
        {({ location }) => {
          console.log(getPathArray(location), itemSlug, getPathArray(location).indexOf(itemSlug));
          const navItemClasses = classnames('side-nav__nav-item', {
            'side-nav__nav-item--open': this.state.open || getPathArray(location).indexOf(itemSlug) !== -1,
            'side-nav__nav-item--active': getPathArray(location).indexOf(itemSlug) !== -1 && !hasSubNav,
          });
          return (
            <li className={navItemClasses}>
              {hasSubNav ? (
                // eslint-disable-next-line
                <a onClick={this.toggleSubNav}>
                  {item.title}{' '}
                  <Icon
                    className="side-nav__nav-item--arrow"
                    name="caret--down"
                    aria-hidden="true"
                    description="Menu arrow icon"
                    alt="Menu arrow icon"
                  />
                </a>
              ) : (
                <Link to={`/${itemSlug}`}>{item.title}</Link>
              )}
              {hasSubNav && <ul className="side-nav__sub-nav">{this.renderSubNavItems(item['sub-nav'], location)}</ul>}
            </li>
          );
        }}
      </Location>
    );
  }
}
